// Entry file for js scripts

	console.log('Go ahead!!!')

	function loadImage(url) {
		return new Promise(resolve => {
			const img = new Image();
			img.src = url;
			img.addEventListener('load', () => {
				resolve(img);
			});
		});
	}

	function getDataFromJSON(nameFile) {
		return fetch(`/database/${nameFile}.json`)
		.then(response => response.json());
	}

	getDataFromJSON('sprite')
	.then( fileJSON => console.log(fileJSON));




	const canvas = document.getElementById('canvas');

	canvas.setAttribute('width', 960);
	canvas.setAttribute('height', 640);

	const context = canvas.getContext('2d');
	context.fillStyle = "orange";
	context.fillRect(80,80,60,25);


	loadImage('../images/sprites.png')
	.then(image => {
		context.drawImage(image, 
			0, 76, 
			39, 50, 
			150, 150, 
			39, 50);
		context.drawImage(image, 
			39, 76, 
			37, 50, 
			200, 150, 
			37, 50);
	});
	


	

